<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-1">
        <div class="row">
          <v-btn class="btn-left" @click="goBack">
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
        </div>
      </div>
      <div class="col-11">
        <div class="row head">
          <div class="col-lg-6 col-md-6 col-sm-12 col-12">
            <h1>Notificaciones</h1>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <table class="table">
              <tr v-for="(r, key) in notifications" :key="key">
                <td class="t-img">
                  <v-img lazy-src="@/assets/Images/Restaurant.png" :src="getImage(r.get('settlement').get('restaurant').get('image'))" alt="restaurant-logo" width="80" height="80" ></v-img>
                </td>
                <td class="t-text">{{ r.get('message') }}</td>
                <td>
                  <button class="t-button" @click="goDetail(r.get('objectId'))">Ver detalles</button>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Parse from 'parse'
  export default {
    data() {
      return {
        loading: false,
        notifications: [],
      };
    },
    created() {
      this.$store.state.title = "Restaurantes";
      this.fetchNotifications()
    },
    methods: {
      goBack() {
        this.$router.push("/");
      },
      goDetail(idRestaurant) {
        this.$router.push({
          name: 'Restaurant',
          params: {
            id: idRestaurant
          }
        });
      },
      async fetchNotifications() {
        const Notifications = Parse.Object.extend("Notification");
        const query = new Parse.Query(Notifications);
        query.include("settlement");
        query.include("settlement.restaurant");
        this.loading = true
        try {
          this.notifications = await query.find();
        } catch (error) {
          console.log(error);
          this.$eventBus.$emit('message', error.message)
        } finally {
          this.loading = false;
        }
      },
      getImage(file) {
        try{
          return file.url()
        }catch(error){
          return ''
        }
        
      },
    },
  };
</script>

